.root {
    position: relative;
    height: 70px;
    color: #000;
}

.label {
    position: absolute;
    bottom: 0;
    font-family: 'WB Hauss', sans-serif;
    font-size: 16px;
}

.labelMin {
    left: 0;
}

.labelMinValue {
    position: relative;
    left: -50%;
}

.labelMax {
    right: 0;
}

.labelMaxValue {
    position: relative;
    right: -50%;
}

.track {
    background-color: #e3e4ea;
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    height: 10px;
    border-radius: 10px;
    cursor: pointer;
}

.trackActive {
    background-color: #9a41fe;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    width: 0;
    border-radius: 10px;
}

.slider {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translate(50%, -50%);
    height: 32px;
    width: 32px;
    border: 6px solid #fff;
    border-radius: 50%;
    box-sizing: border-box;
    background-color: #660ec8;
    box-shadow: 0px 12px 16px 0px rgba(0, 0, 0, .4);
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
    -webkit-tap-highlight-color: transparent;
    cursor: pointer;
}

.sliderLabel {
    position: absolute;
    bottom: 100%;
    left: 50%;
    text-align: center;
    margin-bottom: 8px;
    font-family: 'WB Hauss', sans-serif;
    font-size: 16px;
}

.sliderLabelValue {
    position: relative;
    left: -50%;
}